@use 'sass:math';
@import './libs/functions';
@import './libs/mixins';
@import './libs/reset';
@import './modules/fonts';
@import './modules/vars';

body {
	background-color: var(--color-bg-primary);
	font-family: var(--font-regular);
	color: var(--color-textr-primary);
}


.app {
	display: grid;
	max-width: rem(540);
	margin: 0 auto;
	padding: 15vh rem(10) 5vh;
	gap: rem(30) 0;
}

.title {
	font-family: var(--font-medium);
	font-size: rem(40);
	font-weight: 500;
	text-align: center;
}

.tasks {
	display: grid;
	gap: rem(20);
	padding: rem(20);
	background-color: var(--color-bg-secondary);
	border-radius: rem(8);
	box-shadow: 0 0 rem(16) rgba($color: #6246ea, $alpha: 0.08);
}

.tasks-form {
	display: grid;
	grid-template-columns: 1fr auto;
	gap: 0 rem(20);
	// .tasks-form__input
	&__input {
		width: 100%;
		padding: rem(8);
		border: 0;
		border-radius: rem(4);
		background-color: var(--color-bg-secondary);
		box-shadow: 0 0 rem(8) rgba($color: #6246ea, $alpha: 0.1);
		font-size: rem(16);
		&:focus {
			outline: rem(2) var(--color-accent-primary) solid;
		}
	}
	// .tasks-form__button
	&__button {
		padding: rem(8) rem(16);
		border: 0;
		border-radius: rem(4);
		background-color: var(--color-accent-primary);
		box-shadow: 0 0 rem(8) rgba($color: #6246ea, $alpha: 0.1);
		font-family: var(--font-medium);
		font-size: rem(14);
		font-weight: 500;
		color: var(--color-text-secondary);
		transition: background-color 0.2s ease 0s;
		&:hover {
			background-color: var(--color-accent-primary-hover);
		}
		&:focus-visible {
			outline: rem(2) var(--color-text-primary) solid;
		}
	}
}

.tasks-list {
	border-radius: rem(4);
	box-shadow: 0 0 rem(8) rgba($color: #6246ea, $alpha: 0.1);
	// .tasks-list__item
	&__item {
		padding: rem(5) 0;
		&:not(:last-child) {
			border-bottom: rem(1) rgba($color: #6246ea, $alpha: 0.1) solid;
		}
	}
	// .tasks-list__form
	&__form {
		display: grid;
		grid-template-columns: auto 1fr auto;
		gap: 0 rem(5);
	}
	// .tasks-list__status
	&__status {
		position: relative;
		display: flex;
		align-items: center;
		padding: rem(10) rem(15);
		cursor: pointer;
		& input {
			position: absolute;
			left: 0;
			top: 0;
			appearance: none;
			&:hover + .tasks-list__checkbox {
				stroke: rgba($color: #6246ea, $alpha: 0.5);
			}
			&:checked + .tasks-list__checkbox {
				stroke: var(--color-accent-primary);
			}
		}
	}
	// .tasks-list__checkbox
	&__checkbox {
		width: rem(12);
		height: rem(12);
		padding: rem(2);
		border: rem(1) rgba($color: #6246ea, $alpha: 0.2) solid;
		border-radius: rem(4);
	}
	// .tasks-list__input
	&__input {
		width: 100%;
		padding: rem(5);
		border: rem(1) rgba($color: #6246ea, $alpha: 0.2) solid;
		border-radius: rem(4);
		font-family: var(--font-medium);
		font-size: rem(16);
		font-weight: 500;
		&:focus {
			outline: rem(2) var(--color-accent-primary) solid;
		}
		&:disabled {
			border: rem(1) rgba(0, 0, 0, 0) solid;
			background: transparent;
			color: var(--color-textr-primary);
		}
	}
	// .tasks-list__button
	&__button {
		display: flex;
		align-items: center;
		min-height: rem(40);
		padding: rem(10) rem(15);
		border: 0;
		background: transparent;
		&:hover .tasks-list__icon {
			stroke: var(--color-accent-secondary);
		}
	}
	// .tasks-list__icon
	&__icon {
		stroke: var(--color-text-primary);
		transition: stroke 0.2s ease 0s;
		// .tasks-list__icon_edit
		&_edit {
			width: rem(18);
			height: rem(18);
		}
		// .tasks-list__icon_save
		&_save {
			width: rem(18);
			height: rem(14);
		}
	}
}

.tasks-tools {
	display: flex;
	justify-content: flex-end;
	margin: rem(-10) 0;
	// .tasks-tools__button
	&__button {
		display: flex;
		align-items: center;
		min-height: rem(40);
		padding: rem(10);
		border: 0;
		background: transparent;
		&:hover {
			& .tasks-tools__icon_done {
				stroke: var(--color-accent-primary);
			}
			& .tasks-tools__icon_delete {
				stroke: var(--color-accent-secondary);
			}
		}
	}
	// .tasks-tools__icon
	&__icon {
		stroke: var(--color-text-primary);
		transition: stroke 0.2s ease 0s;
		// .tasks-tools__icon_done
		&_done {
			width: rem(26);
			height: rem(14);
		}
		// .tasks-tools__icon_delete
		&_delete {
			width: rem(18);
			height: rem(18);
		}
	}
}
